import { put, takeLatest } from 'redux-saga/effects';

import { CodePanelActions, savedCode } from '../../constants';
import { templateInitialCode } from '../../utils/templateGenerator';

// Loads in code and dark mode settings from local storage
function* loadCodeFromLocalStorage() {
  if (window.localStorage) {
    const content = localStorage.getItem(savedCode);
    if (content) {
      // Dispatch an action to reducer to update the CodePanel content
      yield put({ type: CodePanelActions.UPDATE_CONTENT, content, updateLocalContent: true });
    }
  }
}

function* loadTemplateCode(action) {
  const content = yield templateInitialCode(
    action.url,
    action.exampleName,
    action.exampleGroupName,
  );
  yield put({ type: CodePanelActions.UPDATE_CONTENT, content, updateLocalContent: true });
}

function* setCodeToLocalStorage(action) {
  // Save the code in action content to local storage
  if (window.localStorage) {
    yield localStorage.setItem(savedCode, action.content);
  }
}

// This saga handles changes to the code panel
export default function* codePanelSaga() {
  yield takeLatest(CodePanelActions.ON_LOAD, loadCodeFromLocalStorage);
  yield takeLatest(CodePanelActions.SAVE_CODE, setCodeToLocalStorage);
  yield takeLatest(CodePanelActions.ON_SELECTION, loadTemplateCode);
}
