import initialState from '../../initialState';
import { DossierSelectionPopUpActions } from '../../constants';

const reducer = (state = initialState.DossierSelectionPopUp, action = {}) => {
  switch (action.type) {
    case DossierSelectionPopUpActions.SET_IS_OPEN:
      return { ...state, isOpen: action.isOpen };
    case DossierSelectionPopUpActions.SET_EXAMPLE:
      return {
        ...state,
        dossierUrl: action.dossierUrl,
        exampleGroupName: action.exampleGroupName,
        exampleName: action.exampleName,
        exampleId: action.exampleId,
        embeddingType: action.embeddingType,
      };
    case DossierSelectionPopUpActions.SET_EMBEDDING_TYPE:
      return {
        ...state,
        embeddingType: action.embeddingType,
      };
    default:
      return state;
  }
};
export default reducer;
