import { createSelector } from 'reselect';

export const selectDossierSelectionPopUp = (state) => state.DossierSelectionPopUp;
export const selectIsOpen = createSelector(
  selectDossierSelectionPopUp,
  (DossierSelectionPopUp) => DossierSelectionPopUp.isOpen,
);
export const selectExampleGroupName = createSelector(
  selectDossierSelectionPopUp,
  (DossierSelectionPopUp) => DossierSelectionPopUp.exampleGroupName,
);
export const selectExampleName = createSelector(
  selectDossierSelectionPopUp,
  (DossierSelectionPopUp) => DossierSelectionPopUp.exampleName,
);
export const selectDossierUrl = createSelector(
  selectDossierSelectionPopUp,
  (DossierSelectionPopUp) => DossierSelectionPopUp.dossierUrl,
);
export const selectExampleId = createSelector(
  selectDossierSelectionPopUp,
  (DossierSelectionPopUp) => DossierSelectionPopUp.exampleId,
);
export const selectEmbeddingType = createSelector(
  selectDossierSelectionPopUp,
  (DossierSelectionPopUp) => DossierSelectionPopUp.embeddingType,
);
