/* eslint-disable import/prefer-default-export */

import { DossierSelectionPopUpActions } from '../../constants';

export const setIsOpen = (isOpen) => ({
  type: DossierSelectionPopUpActions.SET_IS_OPEN,
  isOpen,
});

export const setExample = (example) => ({
  type: DossierSelectionPopUpActions.SET_EXAMPLE,
  dossierUrl: example.dossierUrl,
  exampleGroupName: example.exampleGroupName,
  exampleName: example.exampleName,
  exampleId: example.exampleId,
  embeddingType: example.embeddingType,
});

export const setLinkedExample = (exampleId) => ({
  type: DossierSelectionPopUpActions.SET_LINKED_EXAMPLE,
  exampleId,
});

export const loadPopUp = () => ({
  type: DossierSelectionPopUpActions.ON_LOAD_POPUP,
});

export const setEmbeddingType = (type) => ({
  type: DossierSelectionPopUpActions.SET_EMBEDDING_TYPE,
  embeddingType: type,
});
