import { put, takeLatest } from 'redux-saga/effects';

import {
  DossierSelectionPopUpActions,
  savedCode,
  exampleIdString,
  GoogleAnalyticsActions,
  embeddingTypeString,
} from '../../constants';
import { USE_DEMO_EXAMPLES_FROM_LINK } from '../../constants/google-analytics';
import { embeddingTypes, exampleTemplates } from '../../utils/templateGenerator';

// Save embedding type to local storage
function* setEmbeddingTypeToLocalStorage(action) {
  if (window.localStorage) {
    yield localStorage.setItem(embeddingTypeString, action.embeddingType);
  }
}
// Save the example id to local storage
function* setExampleIdToLocalStorage(action) {
  if (window.localStorage) {
    if (action.exampleId) {
      yield localStorage.setItem(exampleIdString, action.exampleId);
    }
  }
}
// Load the linked example from local storage to the selected example. If it isn't valid use default example.
function* loadExampleFromLocalStorage() {
  if (window.localStorage) {
    let exampleId = window.localStorage.getItem(exampleIdString);
    window.localStorage.removeItem(exampleIdString);
    if (exampleId) {
      try {
        exampleId = decodeURIComponent(exampleId);
      } catch (e) {
        return;
      }
      const templateObjects = Object.values(exampleTemplates);
      for (let index = 0; index < templateObjects.length; index += 1) {
        const group = templateObjects[index];
        const items = Object.values(group.items);
        for (let itemIndex = 0; itemIndex < items.length; itemIndex += 1) {
          const item = items[itemIndex];
          if (item.id === exampleId) {
            // Dispatch an action to reducer to update the selected example
            yield put({
              type: DossierSelectionPopUpActions.SET_EXAMPLE,
              dossierUrl: item.defaultUrl,
              exampleGroupName: group.groupName,
              exampleName: item.name,
              exampleId: item.id,
              embeddingType: item.embeddingType,
            });
            // Dispatch an action to GA saga to record this event
            yield put({
              type: GoogleAnalyticsActions.SEND_GA_EVENT,
              eventLabel: item.name,
              eventName: USE_DEMO_EXAMPLES_FROM_LINK,
            });
            return;
          }
        }
      }
      // Throw user alert when the example couldn't be found
      window.alert(
        'The example could not be found. Please make sure the url and example id are valid.',
      );
    }
  }
}

// Load in the linked example(set popup to open and call load example code), or use local code(set popup to close), or there's no code(set popup to open)
function* onLoadPopUp() {
  if (window.localStorage) {
    // If the local storage doesn't exist or is null use the default dossier iframe embedding type
    const defaultEmbeddingType =
      window.localStorage.getItem(embeddingTypeString) || embeddingTypes.DOSSIER_IFRAME;
    if (window.localStorage.getItem(exampleIdString)) {
      yield put({ type: DossierSelectionPopUpActions.SET_IS_OPEN, isOpen: true });
      yield put({ type: DossierSelectionPopUpActions.ON_LOAD_LINKED_EXAMPLE });
    } else {
      yield put({
        type: DossierSelectionPopUpActions.SET_EMBEDDING_TYPE,
        embeddingType: defaultEmbeddingType,
      });
      if (window.localStorage.getItem(savedCode)) {
        yield put({ type: DossierSelectionPopUpActions.SET_IS_OPEN, isOpen: false });
      } else {
        yield put({ type: DossierSelectionPopUpActions.SET_IS_OPEN, isOpen: true });
      }
    }
  } else {
    yield put({ type: DossierSelectionPopUpActions.SET_IS_OPEN, isOpen: true });
  }
}

// This saga handles changes to the dossier selection popup
export default function* dossierSelectionPopUpSaga() {
  yield takeLatest(
    DossierSelectionPopUpActions.ON_LOAD_LINKED_EXAMPLE,
    loadExampleFromLocalStorage,
  );
  yield takeLatest(DossierSelectionPopUpActions.ON_LOAD_POPUP, onLoadPopUp);
  yield takeLatest(
    DossierSelectionPopUpActions.SAVE_EMBEDDING_TYPE,
    setEmbeddingTypeToLocalStorage,
  );
  yield takeLatest(DossierSelectionPopUpActions.SET_LINKED_EXAMPLE, setExampleIdToLocalStorage);
  yield takeLatest(DossierSelectionPopUpActions.SET_EXAMPLE, setEmbeddingTypeToLocalStorage);
}
